import React, { Component } from "react";
import {
  Table,
  Modal,
  ModalBody
} from "reactstrap";

import MeowfficerSkill from "views/components/Meowfficer/MeowfficerSkill"
import MeowfficerData from "assets/json/MeowfficerData.json";
// import MeowfficerTalent from "assets/json/MeowfficerTalent.json"

import MeowfficerSupport from "assets/AL/MeowfficerSupport.png";
import MeowfficerCommand from "assets/AL/MeowfficerCommand.png";
import MeowfficerTactics from "assets/AL/MeowfficerTactics.png";


export default class MeowfficerModal extends Component {
  render() {
    const modalData            = this.props.modalData;
    const modalOpen            = this.props.modalOpen;
    const modalClose           = this.props.modalClose;
    const openSkillModal       = this.props.openSkillModal;
    const skillModalOpen       = this.props.skillModalOpen;
    const skillModalClose      = this.props.skillModalClose;

    let idx                    = modalData.codex;
    if(idx === null || idx === undefined) {idx = 0}

    const meowfficer           = MeowfficerData[idx]

    // metadata
    const meowfficerBanner     = meowfficer.banner;
    const meowfficerName       = meowfficer.name;
    const meowfficerLogistics  = meowfficer.stats.logistics[0]  + " → " + meowfficer.stats.logistics[1];
    const meowfficerDirectives = meowfficer.stats.directives[0] + " → " + meowfficer.stats.directives[1];
    const meowfficerTactics    = meowfficer.stats.tactics[0]    + " → " + meowfficer.stats.tactics[1];
    const meowfficerType       = meowfficer.type;
    const meowfficerSlot       = meowfficer.slot;
    const meowfficerRarity     = meowfficer.rarity;
    const meowfficerFaction    = meowfficer.faction;
    const meowfficerBuildTime  = meowfficer.timer;

    // skill
    const meowfficerSkill      = meowfficer.skill.name;
    const meowfficerSkillImage = meowfficer.skill.image;
    const meowfficerSkill1     = meowfficer.skill.general.level1;
    const meowfficerSkill2     = meowfficer.skill.general.level2;
    const meowfficerSkill3     = meowfficer.skill.general.level3;
    const meowfficerOpsi1      = meowfficer.skill.opsi.level1;
    const meowfficerOpsi2      = meowfficer.skill.opsi.level2;
    const meowfficerOpsi3      = meowfficer.skill.opsi.level3;

    const skill                = ({
      name   :meowfficerSkill,
      image  :meowfficerSkillImage,
      general:{level1:meowfficerSkill1, level2:meowfficerSkill2, level3:meowfficerSkill3},
      opsi   :{level1:meowfficerOpsi1 , level2:meowfficerOpsi2 , level3:meowfficerOpsi3 },
    })
    return (
      <>
        <Modal isOpen={modalOpen} toggle={modalClose} className="meowfficerModal">
          <ModalBody>
            <div className="text-center catModalHeader">
              <img 
                className="img-fluid"
                referrerPolicy="no-referrer"
                alt="..."
                src={meowfficerBanner}
              />
              <br/>
              <strong>{meowfficerName}</strong>
            </div>
            <Table bordered className="catModalTable catModalTableAlign">
              <thead>
                <tr>
                  <th>
                    <img src={MeowfficerSupport} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
                    <br />Logistics
                  </th>
                  <th>
                    <img src={MeowfficerCommand} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
                    <br />Directives
                  </th>
                  <th>
                    <img src={MeowfficerTactics} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
                    <br />Tactics
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{meowfficerLogistics }</td>
                  <td>{meowfficerDirectives}</td>
                  <td>{meowfficerTactics   }</td>
                </tr>
                <tr onClick={openSkillModal}>
                  <th>Skill</th>
                  <th colSpan="2" className="linkCS">{meowfficerSkill}</th>
                </tr>
                <tr>
                  <th>Skill Type</th>
                  <td colSpan="2">{meowfficerType}</td>
                </tr>
                <tr>
                  <th>Skill Slot</th>
                  <td colSpan="2">{meowfficerSlot}</td>
                </tr>
                <tr>
                  <th>Rarity</th>
                  <td colSpan="2">{meowfficerRarity}</td>
                </tr>
                <tr>
                  <th>Faction</th>
                  <td colSpan="2">{meowfficerFaction}</td>
                </tr>
                <tr>
                  <th>Training Time</th>
                  <td colSpan="2">{meowfficerBuildTime}</td>
                </tr>
              </tbody>
            </Table>
          </ModalBody>
        </Modal>
        <MeowfficerSkill
          skillModalOpen ={skillModalOpen }
          skillModalClose={skillModalClose}
          skill          ={skill          }
        />
      </>
    );
  };
};
